#sidebar {
  margin-top: 50px;
  width: 300px;
  background: #211f1f;
  border: 1px solid black;
  color: white;
}

.player-area {
  display: flex;
  align-items: center;
  width: 200px;
  margin: 20px auto;
  justify-content: space-between;
}
.player-label {
  margin: 0px;
  font-size: 30px;
  font-weight: 700;
}
.score-box {
  display: flex;
  flex-direction: row;
  width: 100px;
  background: rgb(18, 190, 18);
  border: 3px ridge black;
  align-items: center;
  border-radius: 5px;
  padding: 5px;
  padding-left: 10px;
  justify-content: space-evenly;
}

#status {
  padding-top: 75px;
  margin-bottom: 75px;
  font-size: 30px;
}
#clock {
  margin-top: 100px;
}
#black-score,
#white-score {
  font-size: 22px;
  font-weight: 700;
}
#black-score {
  color: black;
}

#black-icon,
#white-icon {
  border-radius: 50px;
  width: 20px;
  height: 20px;
}

#black-icon {
  background-color: black;
}

#white-icon {
  background-color: white;
}

@media screen and (max-width: 918px) {
  #sidebar {
    margin-top: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 496px;
  }
  .player-area {
    width: 150px;
  }
}

@media screen and (max-width: 600px) {
  #sidebar {
    margin-top: 0px;
    width: 360px;
  }
}
