#container {
  margin-top: 150px;
}

button {
  cursor: pointer;
}
#game-title {
  color: white;
}
#start-button {
  width: 200px;
  padding: 10px;
  background: green;
  border: 1px solid black;
  color: white;
  font-size: 22px;
  display: block;
  margin: 20px auto;
}
#how-to {
  color: white;

  font-weight: bold;
  text-decoration: none;
}

@media screen and (max-width: 918px) {
  #container {
    margin-top: 0px;
    margin-bottom: 10px;
  }
}
